* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #cbf0de;
  font-family: Courier New, Courier, monospace;
}

.header {
  background-color: #f58931;
  padding: 2rem;
}

.navigation {
  text-align: center;
  background-color: #243824;
  margin: 0;
  padding: 10px;
}

.navigation a {
  color: #3e9f95;
  text-transform: uppercase;
  padding: 10px;
  font-weight: 600;
  text-decoration: none;
}

.navigation a:hover {
  color: #c5a284;
  filter: drop-shadow(0 0 10px #f5faae);
  font-weight: 600;
  transition: all .4s ease-in-out;
  transform: translate(-20%, -20%);
}

#bioContainer {
  color: #0d0d0d;
  background-color: #a4e2bbfc;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem;
  padding: 2rem;
}

#bioContainer div {
  flex: 0 50%;
}

#UserNameAndReg, #imgDiv {
  text-align: center;
}

#userNameDiv {
  color: #002c42;
  font-size: 22px;
}

#imgDiv img {
  border: 6px solid #09141e;
}

#userBox {
  color: #000;
}

#imgDiv img {
  max-width: 20rem;
}

#deleteButton {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #64212c;
  border: 5px solid #0000;
  border-radius: 100px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

#deleteButton:hover {
  filter: drop-shadow(0 0 8px #e45959);
  background-color: #9c0909;
  transition: all .4s ease-in-out;
}

#forms {
  opacity: 1;
  filter: drop-shadow(10px 10px 4px #020307);
  width: 60rem;
  height: 40rem;
  background-color: #1d253b;
  background-image: url("https://media.istockphoto.com/id/1444223113/vector/cartoon-dogs-and-puppies-funny-characters-group-outdoor.jpg?s=612x612&w=0&k=20&c=gpzSaFEZ8irwIwqFsWGyRLWeiuoBdtn3KHRUPtWWFJg=");
  background-size: cover;
  border-radius: 20px;
  justify-content: center;
  margin-right: -50%;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 600px) {
  #forms {
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    display: flex;
  }

  #twoforms {
    width: 100%;
  }

  #registerForm.twoForms, #loginForm.twoForms {
    width: 80%;
  }

  #signupButton {
    position: none;
    width: 120px;
    margin-top: 35%;
    margin-right: 500px;
  }

  #loginButton {
    position: none;
    width: 120px;
    margin-top: 35%;
    margin-right: 10px;
  }
}

#forms h2 {
  text-align: center;
  text-transform: uppercase;
  color: #050505;
  text-shadow: -1px -1px #317392, 1px -1px #317392, -1px 1px #163646, 1px 1px #163646;
  filter: drop-shadow(0 0 8px #fde0b3);
  width: 50%;
  background-color: #fff;
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: 300;
}

#forms h4 {
  text-align: center;
  color: #161a25;
  background: radial-gradient(circle, #1d475b 0%, #fdd5d600 100%);
  margin-top: 20px;
  font-weight: bold;
}

#loginForm, #registerForm {
  width: 50%;
  opacity: 1;
  padding: 10px;
}

#loginForm:hover, #registerForm:hover {
  opacity: 1;
  transition: all .4s ease-in-out;
}

.twoForms input[type="text"], .twoForms input[type="password"], .twoForms select {
  width: 100%;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 10px;
  margin: 8px 0;
  padding: 12px 20px;
}

.twoForms input[type="text"]:focus, .twoForms input[type="password"]:focus, .twoForms select:focus {
  border: 3px solid #f3ce95;
  outline: none;
}

.twoForms input {
  width: 100%;
}

.twoForms button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #038d53;
  border: 5px solid #0000;
  border-radius: 100px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.twoForms button:hover {
  filter: drop-shadow(0 0 8px #fde0b3);
  background-color: #f3ce95;
  transition: all .4s ease-in-out;
  transform: translate(-20%, -20%);
}

#loginButton {
  margin-right: -50%;
  position: absolute;
  top: 75%;
  left: 20%;
  transform: translate(-20%, -20%);
}

#signupButton {
  margin-right: -50%;
  position: absolute;
  top: 75%;
  left: 70%;
  transform: translate(-20%, -20%);
}

.yourPlace {
  color: #e2762e;
  margin: auto;
}

.addMessageToForum {
  text-align: center;
  margin: auto;
}

#sendMessageToForum {
  color: #020202;
  text-align: center;
  cursor: pointer;
  background-color: #8eac61e0;
  border: 5px solid #0000;
  border-radius: 20px;
  margin-bottom: 10px;
  margin-left: 10px;
  padding: 10px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

#postWrapper {
  text-align: center;
  width: 50%;
  background-color: #d2e69f86;
  border-radius: 15px;
  margin: auto;
}

.forumPost {
  border-bottom: 2px solid #161a25;
  padding: 20px;
}

.postName {
  color: #000;
}

.postMessage {
  color: #046e27;
}

.button {
  color: #000;
  text-align: center;
  cursor: pointer;
  background-color: #f1913e;
  border: 5px solid #0000;
  border-radius: 100px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
  position: absolute;
  right: 50px;
}

.button:hover, #sendMessageToForum:hover {
  filter: drop-shadow(0 0 8px #fde0b3);
  background-color: #f1743e;
  transition: all .4s ease-in-out;
}

#anyUserContainer, #showUserBio {
  color: #000;
  text-align: center;
  background-color: #bdf7c5;
  border: 4px solid #f2952b;
}

#anyUserContainer {
  margin-top: 50px;
}

#anyUserContainer button {
  color: #010101;
  text-align: center;
  cursor: pointer;
  background-color: #e67e38;
  border: none;
  border-radius: 5px;
  margin: 5px 2px;
  padding: 5px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

#anyUserContainer button:hover {
  filter: drop-shadow(0 0 8px #3c7d9b);
  background-color: #fb8764;
  border: 2px solid #0000;
  transition: all .4s ease-in-out;
}

#showUserBio img {
  width: 20%;
  border: 4px solid #09141e;
}

#editDiv {
  background-color: #7fffbde0;
  border-left: 4px solid #214d80;
  border-right: 4px solid #214d80;
}

input#editPassword, input#confirmChangePassword, select#editColor, select#changeImg, textarea#bioInformation {
  border: none;
  border-radius: 3px;
  margin-top: 3px;
  padding: 4px;
}

#editBtn, button#editButton, button#deleteButton {
  background-color: #fb8764;
  border: none;
  border-radius: 5px;
  padding: 5px;
}

